import axios from "axios";

let url = "http://212.107.253.70:8090";

export default {
  state: {
    user: {},
    isLoading: false
  },
  actions: {
    async loginUser({ commit }, { login, password }) {
      commit("setUserLoadStatus", true);

      let response = await axios.post(url + "/site/login", {
        email_phone: login,
        password: password
      });
      if (response.data.status === 0) {
        let user = {
          id_user: response.data.id_user,
          id_profile: response.data.id_profile,
          username: response.data.username,
          fio: response.data.fio,
          email: response.data.email,
          avatar: response.data.avatar
        };

        commit("setUser", user);
        return 1;
      }
      console.log("Error");
      return 0;
    },
    async logoutUser() {
      return await axios.post(url + "/site/logout");
    }
  },
  getters: {
    userName(state) {
      return state.username;
    },
    userAvatar(state) {
      return state.avatar;
    },
    userStatus(state) {
      return state.user.status;
    }
  },
  mutations: {
    setUserLoadStatus(state, status) {
      state.user.status = status;
    },
    setUser(state, user) {
      state.user = user;
    }
  }
};
